@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf"), url("Avenir-Heavy.01d9ab74.ttf");
  font-style: normal;
  font-weight: 300, 400;
}

* {
  box-sizing: border-box;
}

body {
  --text-placholder-color: #3d4e61;
  --border-color: #e1e7ed;
  --bg-color: #fff;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

input {
  border: #0000;
  padding: 0;
}

.page__search {
  margin-top: 20px;
}

.search-bar {
  position: relative;
}

.search-bar__text-input {
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  background: var(--bg-color);
  color: var(--text-placholder-color);
  border-radius: 4px;
  font-family: Avenir, sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 19px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar__text-input::placeholder {
