}

.search-bar__text-input--big {
  width: 100%;
  padding-top: 24px;
  padding-bottom: 25px;
  padding-left: 61px;
}

.search-bar__text-input--small {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 11px;
  padding-left: 32px;
  font-weight: normal;
}

.search-bar__text-input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__text-input:focus {
  border: 1px solid var(--border-color);
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  box-shadow: 0 4px 4px #00000040;
